.matrix {
  display: grid;
  gap: 15px;
  justify-items: start;
}
.matrix .cell {
  width: auto;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-align: center;
  /* Ẩn các ô không được chọn */
  /* Icon styling */
  /* Animation cho status chuyển đổi */
  /* Animation khi bỏ chọn */
}
.matrix .cell .cell-name {
  position: absolute;
  font-size: 10px;
}
.matrix .cell .hidden {
  visibility: hidden;
}
.matrix .cell .icon {
  width: 28px;
  height: 28px;
}
.matrix .cell.status-changed {
  animation: statusChange 0.3s forwards;
}
.matrix .cell.status-deselected {
  animation: statusDeselect 0.3s forwards;
}
